@import "sanitize";

$grey: #555;
$blue: #38A6F9;
$red: #cd2d18;
$brand: #001119;
// $text-color: #5c5c5c;
$text-color: #000;
$bg-color: #f8f8f5;

body {
  font-family: "Avenir Next", "Segoe UI", "Helvetica Neue", Arial, "Hiragino Sans GB", "PingFang SC", "Heiti SC", "Microsoft YaHei UI", "Microsoft YaHei", "Source Han Sans", sans-serif;
  -webkit-font-smoothing: antialiased;
  color: $text-color;
}

.sr .header,
.sr .jobs,
.sr .header .animation-item,
.sr .feature-list .item,
.sr .member-list .item  {
  visibility: hidden;
}

.header {
  position: relative;
  display: flex;
  justify-content: center;
  align-items: center;
  width: 100%;
  height: 100vh;
  text-align: center;
  overflow: hidden;
  color: #fff;
  background-color: #fff;

  .header-bg {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background-image: url('../images/dark-bg-2.jpg');
    background-size: cover;
    transition: transform 36s;
    transition-timing-function: linear;
  }

  .caption {
    width: 480px;
  }

  .title {
    font-size: 62px;
    font-weight: lighter;
    font-style: italic;
    letter-spacing: 0.1em;
    margin-bottom: 12px;
  }

  .slogan {
    font-size: 18px;
  }

  .description {
    font-size: 14px;
    margin-top: 1em;
  }

  .down {
    position: absolute;
    bottom: 1em;
    left: 50%;
    margin-left: -8px;
    animation: fadeIn 2s;
    animation-delay: 2s;
    animation-fill-mode: both;

    .icon-down {
      width: 16px;
      height: 16px;
      fill: #fff;
      animation: bounce 3s infinite;
    }
  }
}

.feature,
.team,
.jobs {
  padding: 4em 0;
  color: $text-color;

  &:nth-child(2n) {
    background-color: rgb(249, 249, 247);
  }

  .container {
    position: relative;
    display: flex;
    align-items: center;
    flex-direction: column;
    width: 1024px;
    margin: 0 auto;

    > .caption {
      position: relative;
      margin-bottom: .3em;
      letter-spacing: .02em;
      font-size: 42px;
      font-style: italic;
      color: $brand;
      text-align: center;
    }

    > .description {
      width: 540px;
      text-align: justify;
      line-height: 1.7;
      color: $grey;
      margin-bottom: 1em;
    }
  }
}

.feature .feature-list {
  display: flex;
  flex-wrap: wrap;
  margin-top: 1em;
  margin: 0 -1.5em 0 -1.5em;

  .item {
    position: relative;
    display: flex;
    width: 33.33%;
    padding: 0 1.5em;
    padding-left: 52px;
    margin-top: 3em;

    .icon {
      position: absolute;
      left: 0;
      height: 30px;
      width: 30px;
      fill: $blue;
    }

    .title {
      font-size: 20px;
      font-weight: normal;
      margin-bottom: .5em;
    }

    .description {
      width: 260px;
      color: $grey;
      font-size: 14px;
    }
  }
}

.team {
  .member-list {
    display: flex;
    flex-wrap: wrap;
    justify-content: space-around;

    .item {
      width: 248px;
      padding: 0 1.5em;
      margin-top: 3em;

      .avatar {
        max-width: 100%;
        width: 200px;
        height: 240px;
        border: 1px solid #ececec;
      }

      .title {
        text-align: center;
        color: #999;
      }

      .name {
        margin-top: 1em;
        text-align: center;
      }
    }
  }
}

.jobs {
  position: relative;
  width: 1024px;
  margin: 0 auto;
  padding: 1em;
  border-radius: 2px;
  margin-bottom: 4em;

  .bg {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    border-radius: 3px;
    background-image: url('../images/background-3.jpg');
    background-size: cover;
  }

  .container {
    > .caption,
    > .description {
      color: #fff;
    }

    > .caption {
      font-size: 36px;
    }

    > .description {
      font-size: 14px;
    }
  }
}

.footer {
  position: relative;
  background-color: #fff;
  width: 100%;
  text-align: center;
  padding: 2em 0;
  border-top: 1px solid #ececec;

  .contact {
    color: #5c5c5c;
  }

  .record {
    font-size: 12px;
    color: #999;
    margin-top: .5em;
  }
}

@keyframes bounce {
  0%, 20%, 50%, 80%, 100% {transform: translateY(0);}
  40% {transform: translateY(-10px);}
  60% {transform: translateY(-6px);}
}

@keyframes fadeIn {
  0% {
    transform: translateY(-20px);
    opacity: 0;
  }

  100% {
    transform: translateY(0);
    opacity: 1;
  }
}
